var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-md-6"},[_c('div',{staticClass:"page-title"},[_c('h2',[_vm._v("CREE INVESTING $1 B IN NORTH CAROLINA")]),_c('p',[_vm._v("A BEAUTIFUL CITY IN CHINA!")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-8 col-xs-12"},[_c('div',{staticClass:"blog-content-dv"},[_c('article',{staticClass:"post"},[_c('span',{staticClass:"site-author"},[_c('figure',{staticClass:"avatar"},[_c('a',{attrs:{"href":"#"}},[_c('img',{attrs:{"src":"/avatar.png","alt":""}})])])]),_c('header',{staticClass:"blog-entry-header"},[_c('h3',{staticClass:"blog-entry-title"},[_vm._v("From Business Facilities")])]),_c('div',{staticClass:"entry-meta"},[_c('span',{staticClass:"entry-date"},[_c('a',{attrs:{"href":""}},[_c('p',[_vm._v("May 9, 2019")])])])]),_c('a',{staticClass:"post-img-a pst-img-blog",attrs:{"href":"#"}},[_c('img',{attrs:{"src":require("./img6.jpg"),"alt":""}})]),_c('div',{staticClass:"this-main-blog-cont"},[_c('p',[_vm._v("Cree, Inc. will invest up to $1 billion in the expansion of its silicon carbide capacity with the development of a state-of-the-art, automated 200mm silicon carbide fabrication facility and a materials mega factory at its U.S. campus headquarters in Durham, NC. The investment is part of the companys long-term growth strategy, and marks its largest investment to date in fueling its Wolfspeed silicon carbide and GaN on silicon carbide business. Upon completion in 2024, the facilities will substantially increase the companys silicon carbide materials capability and wafer fabrication capacity, allowing wide bandgap semiconductor solutions that enable the dramatic technology shifts underway within the automotive, communications infrastructure and industrial markets.")]),_c('br'),_c('p',[_vm._v("We continue to see great interest from the automotive and communications infrastructure sectors to leverage the benefits of silicon carbide to drive innovation. However, the demand for silicon carbide has long surpassed the available supply. Today, we are announcing our largest-ever investment in production to dramatically increase this supply and help customers deliver transformative products and services to the marketplace, said Gregg Lowe, CEO of Cree. This investment in equipment, infrastructure and our workforce is capable of increasing our silicon carbide wafer fabrication capacity up to 30-fold and our materials production by up to 30-fold compared to Q1 of fiscal year 2017, which is when we began the first phase of capacity expansion. We believe this will allow us to meet the expected growth in Wolfspeed silicon carbide material and device demand over the next five years and beyond.")]),_c('br'),_c('ul',[_c('li',[_vm._v("$450 million to build out Crees North Fab production facility in an empty building on the Durham campus")]),_c('li',[_vm._v("$450 million to convert a smaller fab to a second silicon carbide crystal growth facility")]),_c('li',[_vm._v("$10 million in other investments")])]),_c('br'),_c('br'),_c('p',[_vm._v("The plan delivers additional capacity for its industry-leading Wolfspeed silicon carbide business with the build out of an existing structure as a 253,000 square-foot, 200mm power and RF wafer fabrication facility as an initial step to serve the projected market demand. The new North Fab is designed to be fully automotive qualified and will provide nearly 18 times more surface area for manufacturing than exists today, initially opening with the production of 150mm wafers. The company will convert its existing Durham fabrication and materials facility into a materials mega factory.")]),_c('br'),_c('p',[_vm._v("These silicon carbide manufacturing mega-hubs will accelerate the innovation of todays fastest growing markets by producing solutions that help extend the range and reduce the charge times for electric vehicles, as well as support the rollout of 5G networks around the world, said Lowe. We believe that this represents the largest capital investment in the history of silicon carbide and GaN technologies and production with a fiscally responsible approach. By using existing facilities and installing a majority of refurbished tools, we believe we will be able to deliver a state-of-the-art 200mm capable fab at approximately one-third of the cost of a new fab.")]),_c('br'),_c('p',[_vm._v("The expanded campus also creates high-tech job opportunities and will serve as an advanced manufacturing workforce development initiative. Cree plans to partner with state and local community and four-year colleges to develop training programs to prepare its workforce for the long-term, high-quality employment and growth opportunities the new facilities will present.")]),_c('br'),_c('h2',[_vm._v("CreeSiC, 5G")]),_c('br'),_c('p',[_vm._v("Cree, Inc. 10SiC200mm SiCWolfspeed SiCGaN-on-SiC2024SiCCreeGregg LoweSiC2017SiC3030Wolfspeed SiC5Wolfspeed SiC253,000200mmNorth Fab18150mm")]),_c('br'),_c('p',[_vm._v("CreeGregg LoweSiC EV5GSiCGaN200mm fabfab1/3CreeCreeVolkswagen GroupFASTFuture Automotive SupplyTracksSiC")]),_c('br'),_c('p',[_vm._v("FASTEVSiCSiCEVEVCreetier oneSiC")])])])])])])])}]

