                                <p>According to the event organizing committee, under the guidance of the China Innovation and Entrepreneurship Competition Organizing Committee, the 2019 Innovation Songhu Entrepreneurship Competition focuses on industries and promotes settlement as its main goal. It adopts the "1 + 1 + N" model to fully integrate with China's innovation and entrepreneurship competitions.</p>
